.turbo_navbar {
    width: 100%;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 2rem;
    border-bottom: 2px solid #1A202C;
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    z-index: 99;
}

.turbo_navbar-links_logo {
    margin-right: 0.5rem;

    /* slide animation */
    -webkit-animation: slide-in-left 0.7s both;
	        animation: slide-in-left 0.7s both;
}

/* slide keyframe anim */
@-webkit-keyframes slide-in-left {
    0% {
      -webkit-transform: translateX(-1000px);
              transform: translateX(-1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }
  @keyframes slide-in-left {
    0% {
      -webkit-transform: translateX(-1000px);
              transform: translateX(-1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }


.turbo_navbar-links_logo img {
    height: 50px;
}

.turbo_navbar-links {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
}

.turbo_navbar-links_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    white-space: nowrap;
}

.turbo_navbar-links_container p, 
.turbo_ham-menu_container-links p {
    color: #eaeaea;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: calc(1rem + 2px);
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1.5rem;
    cursor: pointer;
    transition: ease 0.4s;
}

.turbo_navbar-links_container p:hover, 
.turbo_ham-menu_container-links p:hover {
    color: #3d6a85;
}

.turbo_ham-menu {
    display: none;
    z-index: 100;
    position: relative;
}

.turbo_ham-menu svg{
    cursor: pointer;
}

.turbo_ham-menu_container {
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
    padding: 1rem;
    position: absolute;
    top: 40px;
    right: -20px;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.turbo_ham-menu_container2 {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.turbo_ham-menu_container-links p {
    margin: 1.5rem 0;
}

/* media queries */
@media screen and (max-width: 786px) {
    .turbo_navbar-links_container {
        display: none;
    }
    .turbo_ham-menu {
        display: flex;
    }
}


@media screen and (max-width: 425px) {
    .turbo_navbar-links_container {
        display: none;
    }
    .turbo_ham-menu {
        display: flex;
    }
}