.logo-border {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 7rem;
}

.logo-img {
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #fff;
    border-radius: 30px;
    width: calc(5 * 100px);

    /* shadow animation */
    -webkit-animation: shadow-drop-center 5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.4s both, slide-in-blurred-top 0.4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
	animation: shadow-drop-center 5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.4s both, slide-in-blurred-top 0.4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}


/* shadow key anim */
@-webkit-keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
        -webkit-box-shadow: 0px 0px 59px 1px rgba(147,177,166,1);
        -moz-box-shadow: 0px 0px 59px 1px rgba(147,177,166,1);
        box-shadow: 0px 0px 59px 1px rgba(147,177,166,1);
    }
  }
  @keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
        -webkit-box-shadow: 0px 0px 59px -5px rgba(147,177,166,1);
        -moz-box-shadow: 0px 0px 59px -5px rgba(147,177,166,1);
        box-shadow: 0px 0px 59px -5px rgba(147,177,166,1);
    }
  }


  @-webkit-keyframes slide-in-blurred-top {
    0% {
      -webkit-transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
              transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
      -webkit-transform-origin: 50% 0%;
              transform-origin: 50% 0%;
      -webkit-filter: blur(40px);
              filter: blur(40px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0) scaleY(1) scaleX(1);
              transform: translateY(0) scaleY(1) scaleX(1);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      -webkit-filter: blur(0);
              filter: blur(0);
      opacity: 1;
    }
  }
  @keyframes slide-in-blurred-top {
    0% {
      -webkit-transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
              transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
      -webkit-transform-origin: 50% 0%;
              transform-origin: 50% 0%;
      -webkit-filter: blur(40px);
              filter: blur(40px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0) scaleY(1) scaleX(1);
              transform: translateY(0) scaleY(1) scaleX(1);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      -webkit-filter: blur(0);
              filter: blur(0);
      opacity: 1;
    }
  }


.logo-img img {
    height: 130px;
}

.head-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.gradient__txt {
    font-family: var(--font-family);
    text-align: center;
    font-weight: bolder;
    font-size: 3.5rem;
    padding-bottom: 2rem;
}

.card-container {
    font-family: var(--font-family);
    padding: 6rem 1rem;
    display: flex;
    gap: 40px;
    justify-content: center;
    align-items: center;
}

.card-head {
    width: calc(5 * 62px);
    display: flex;
    justify-content: center;
}

.card-title {
    color: #fff;
    padding-right: 0.5rem;
}

.card-details h2{
    font-size: 1.1rem;
    color: #fff;
    padding-bottom: 1rem;
}

.card-details p{
    font-size: 0.9rem;
    color: var(--color-sec-txt);
    text-align: justify;
}

.head-content_btn {
    padding-top: 2rem;
    padding-bottom: 10rem;
}

@media screen and (max-width: 769px) {
    .logo-img img {
        height: 75px;
    }

    .logo-img {
        margin-top: 5%;
        width: calc(3 * 95px);
    }

    .gradient__txt {
        font-size: 2.4rem;
    }

    .card-head {
        width: calc(5 * 50px);
    }
}

@media screen and (max-width: 425px) {
    .gradient__txt {
        text-align: left;
        font-size: 1.8rem;
    }
}