.footer-section {
    font-family: var(--font-family);
    background: radial-gradient(circle at 100% 94%, rgb(35, 48, 77) 15%, rgba(26, 32, 44, 1) 56%);;
    gap: 30px;
    border-top: 2px solid #1A202C;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: #eaeaea;
    font-size: 0.8rem;
    padding: 4rem 4rem;
    text-align: center;
}
.footer-logo img {
    height: 40px;
}
.footer-download img {
    height:28px;
}
.footer-logo_company {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.footer-logo_company > * {
    padding: 0.8rem 0;
}
.footer-nav_links {
    display: flex;
    white-space: nowrap;
}
.footer-nav_links a{
    padding: 1rem 1rem;
    transition: ease 0.6s;
}
.footer-nav_links a:hover{
    color: #3d6a85;
}
.footer-media {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
}
.footer-media_item {
    margin-bottom: 2rem;
}
.footer-media > * {
    padding: 0.5rem 0;
}
.footer-media_item a{
    padding: 0 1rem;
    transition: ease 0.6s;
}
.footer-media_item a:hover{
    color: #3d6a85;
}
.footer-download a {
    padding: 0 1rem;
}